import { Fragment, useEffect, useState } from "react";
import { Popover, Transition } from "@headlessui/react";
import { Bars3Icon, XMarkIcon } from "@heroicons/react/24/outline";
import { PlayCircleIcon } from "@heroicons/react/24/solid";
import studiosevenlogo from "../assets/studiosevenlogo.svg";
import {
  goToPlaylistSection,
  goToContactSection,
} from "../utils/scrollFunctions";
import Reveal from "../shared/Reveal";
import ButtonWrapper from "../shared/SpotLightButton";

export default function NavBar({ changeAndPlayStream }) {
  function handlePlaydemo() {
    goToPlaylistSection();
    changeAndPlayStream(0);
  }

  const [isflipped, setIsflipped] = useState(false);


  useEffect(() => {
    const scrollHandler = () => {
      setIsflipped(window.pageYOffset > 30);
    };

    window.addEventListener("scroll", scrollHandler);

    return () => window.removeEventListener("scroll", scrollHandler);
  }, [setIsflipped]);

  return (
    <Popover as="header" className="fixed top-0 left-0 w-full z-50">
      <Reveal
        hidden={{ opacity: 0, y: -100 }}
        visible={{ opacity: 1, y: 0 }}
        className={`${isflipped ? "bg-gray-700/70 backdrop-blur-md" : "bg-gray-900"} tranmsition duration-200 w-full py-4 sm:py-6 relative flex items-center justify-center px-6 rounded-[100vw] max-w-[95%] mx-auto mt-[18px]`}>
        <div className="flex items-center justify-between w-full max-w-7xl">
          {/* logo */}
          <Reveal
            hidden={{ opacity: 0, x: -100 }}
            visible={{ opacity: 1, x: 0 }}
            width="full"
          >
            <a href="/" >
              <span className="sr-only">Your Company</span>
              <img className="w-40" src={studiosevenlogo} alt="logo" />
            </a>
          </Reveal>

          {/* listen and get started button */}
          <div className="hidden md:flex items-center justify-center gap-4 whitespace-nowrap text-white">
            <Reveal
              hidden={{ opacity: 0, y: -100 }}
              visible={{ opacity: 1, y: 0 }}
              width="full"
            >
              <ButtonWrapper text="Listen to our Demo" icon={<PlayCircleIcon className="w-6 max-w-[1.4rem]" />} onClick={handlePlaydemo} />
            </Reveal>
            <Reveal
              hidden={{ opacity: 0, y: 100 }}
              visible={{ opacity: 1, y: 0 }}
              width="full"
            >
              <ButtonWrapper text="Get Started" onClick={goToContactSection} bgColor="white" />
            </Reveal>
          </div>

          {/* menu btn */}
          <div className="-mr-2 flex items-center md:hidden">
            <Popover.Button className="inline-flex items-center justify-center rounded-md bg-gray-900 p-2 text-white hover:bg-gray-800">
              <span className="sr-only">Open main menu</span>
              <Bars3Icon className="h-6 w-6" aria-hidden="true" />
            </Popover.Button>
          </div>
        </div>

      </Reveal>

      <Transition
        as={Fragment}
        enter="duration-150 ease-out"
        enterFrom="opacity-0 scale-95"
        enterTo="opacity-100 scale-100"
        leave="duration-100 ease-in"
        leaveFrom="opacity-100 scale-100"
        leaveTo="opacity-0 scale-95"
      >
        <Popover.Panel
          focus
          className="absolute inset-x-0 top-0 origin-top transform p-2 transition md:hidden"
        >
          <div className="overflow-hidden rounded-lg bg-gray-800 shadow-md ring-1 ring-black ring-opacity-5">
            <div className="flex items-center justify-between px-5 pt-4">
              <div></div>
              <div className="-mr-2">
                <Popover.Button className="inline-flex items-center justify-center rounded-md bg-gray-700 p-2 text-white hover:bg-gray-600 focus:outline-none">
                  <span className="sr-only">Close menu</span>
                  <XMarkIcon className="h-6 w-6" aria-hidden="true" />
                </Popover.Button>
              </div>
            </div>
            <div className="pt-5 pb-6 flex flex-col items-center gap-4 text-white w-[85%] mx-auto">
              <button
                onClick={handlePlaydemo}
                className="w-full bg-teal-400 hover:bg-teal-400 flex items-center justify-center gap-1 text-center text-black font-semibold px-6 py-2 rounded-lg outline-none"
              >
                Listen to our Demo
                <PlayCircleIcon className="w-6 max-w-[1.4rem]" />
              </button>
              <button
                onClick={goToContactSection}
                className="w-full font-medium bg-white hover:bg-gray-100 text-gray-900 px-8 py-2 rounded-lg"
              >
                Get Started
              </button>
            </div>
          </div>
        </Popover.Panel>
      </Transition>
    </Popover >
  );
}
