import quoteIcon from "../assets/logos/quote/quotationMarks.svg";
import Reveal from "../shared/Reveal";

export default function Quote() {
  return (
    <div className="bg-gray-900 text-white grid place-items-center py-28">
      <div className="w-[min(90%,50rem)] flex flex-col items-center">
        <Reveal>
          <img
            className="w-16 sm:w-20 mb-11"
            src={quoteIcon}
            alt="quotation marks"
          />
        </Reveal>
        <Reveal>
          <h2 className="text-center text-xl sm:text-2xl md:text-3xl lg:text-4xl font-bold mb-9">
            “if you didn’t know, you’d never guess the output was entirely
            produced and presented by AI - strong work”
          </h2>
        </Reveal>
        <Reveal>
          <p className="font-medium mb-1">Scott Williams</p>
        </Reveal>
        <Reveal>
          <span className="text-center">
            Group Content Director, Wireless Ireland
          </span>
        </Reveal>
      </div>
    </div>
  );
}
