import checkListIcon from "../assets/logos/toolsSection/check-list.svg";
import databaseImg from "../assets/img/tools/database.png";
import womanImg from "../assets/img/tools/woman.png";
import micImg from "../assets/img/tools/mic.png";
import factoryImg from "../assets/img/tools/factory.png";
import {
  goToPlaylistSection,
  goToContactSection,
} from "../utils/scrollFunctions";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const toolsData = [
  {
    title: "Broadcast Content Engine",
    description:
      "Studio Seven has developed a bespoke content engine using carefully selected sources to ensure the content is under control",
    features: [
      "Bespoke broadcast content engine",
      "Verified news & information sources",
      "Continuously updated content",
    ],
    img: databaseImg,
    toolBtn: { text: "Get Started", func: goToContactSection },
  },
  {
    title: "Broadcast Language Model",
    description:
      "The AI presenters get your radio station’s brand and connect with your listener’s attitude, values and language",
    features: [
      "Natural language",
      "Professional training",
      "Engaging story-telling",
    ],
    img: womanImg,
    toolBtn: {
      text: "Listen to the Example Playlist",
      func: goToPlaylistSection,
    },
  },
  {
    title: "Broadcast Voice Model",
    description:
      "Use our voices, trained by professional broadcasters, or use your own presenter voices for a uniquely familiar sound",
    features: [
      "Professional broadcast voices",
      "Voices for shows and bulletines",
      "Market-exclusivity available",
    ],
    img: micImg,
    toolBtn: {
      text: "Listen to the Example Playlist",
      func: goToPlaylistSection,
    },
  },
  {
    title: "Playout System Integration",
    description:
      "Studio Seven AI can provide its own playout platform, or we can integrate with the playout system of your choice",
    features: [
      "Bespoke playout solution",
      "Seamless integration with all major playout systems",
      "Industrial grade platform",
    ],
    img: factoryImg,
    toolBtn: { text: "Get Started", func: goToContactSection },
  },
];

export default function ToolsSection() {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2900,
    pauseOnHover: true,
    arrows: false,
  };

  return (
    <div className="mx-auto bg-gray-900 p-8 sm:p-12">
      <h2 className="mb-20 mx-auto text-center text-2xl sm:text-4xl w-[min(90%,48rem)] font-bold text-white">
        Built by radio professionals to create an authentic radio experience
      </h2>
      <div className="mx-auto max-w-7xl shadow-lg p-6 sm:p-12 bg-white rounded-lg">
        <Slider {...settings}>
          {toolsData.map(({ title, description, features, img, toolBtn }) => (
            <div key={title}>
              <div className="grid lg:grid-cols-2 items-center justify-center gap-8 lg:gap-12">
                <div className="flex flex-col justify-center">
                  <h3 className="text-gray-900 text-xl sm:text-2xl font-bold mb-3 sm:mb-4">
                    {title}
                  </h3>
                  <p className="text-gray-900 font-medium mb-4 sm:mb-5">
                    {description}
                  </p>
                  {features.length > 0 && (
                    <ul className="flex flex-col gap-2 sm:gap-4 mb-8">
                      {features.map((f, index) => (
                        <div
                          key={index}
                          className="flex gap-2 items-center justify-start"
                        >
                          <img
                            className="w-5 rounded-full"
                            src={checkListIcon}
                            alt="check list"
                          />
                          <span>{f}</span>
                        </div>
                      ))}
                    </ul>
                  )}
                  <button
                    onClick={toolBtn.func}
                    className="text-sm sm:text-base font-bold rounded-md hover:bg-gray-200 bg-teal-200 text-gray-900 px-6 sm:px-8 py-3"
                  >
                    {toolBtn.text}
                  </button>
                </div>
                  <img className="mx-auto w-172 sm:w-96 rounded-lg" src={img} alt={title} />
              </div>
            </div>
          ))}
        </Slider>
      </div>
    </div>
  );
}
