import { PlayCircleIcon } from "@heroicons/react/24/outline";
import { stagger, useAnimate } from "framer-motion";

const randomNumberBetween = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
};

function AnimatedButton({ className, string, ...props }) {
  const [scope, animateFn] = useAnimate();

  const onButtonClick = () => {
    const sparkles = Array.from({ length: 20 });
    const sparklesAnimation = sparkles.map((_, index) => [
      `.sparkle-${index}`,
      {
        x: randomNumberBetween(-100, 100),
        y: randomNumberBetween(-100, 100),
        scale: randomNumberBetween(1.5, 2.5),
        opacity: 1,
      },
      {
        duration: 0.4,
        at: "<",
      },
    ]);

    const sparklesFadeOut = sparkles.map((_, index) => [
      `.sparkle-${index}`,
      {
        opacity: 0,
        scale: 0,
      },
      {
        duration: 0.3,
        at: "<",
      },
    ]);

    const sparklesReset = sparkles.map((_, index) => [
      `.sparkle-${index}`,
      {
        x: 0,
        y: 0,
      },
      {
        duration: 0.000001,
      },
    ]);

    animateFn([
      ...sparklesReset,
      [".letter", { y: -32 }, { duration: 0.2, delay: stagger(0.05) }],
      ["button", { scale: 0.8 }, { duration: 0.1, at: "<" }],
      ["button", { scale: 1 }, { duration: 0.1 }],
      ...sparklesAnimation,
      [".letter", { y: 0 }, { duration: 0.000001 }],
      ...sparklesFadeOut,
    ]);
  };
  const text = string?.split(/(\s+)/);

  return (
    <div ref={scope} {...props}>
      <button onClick={onButtonClick} className={className}>
        <span className="sr-only">{string}</span>
        <span
          className="flex items-center justify-center w-full gap-[1px]"
          aria-hidden
        >
          {text.map((letter, index) => (
            <span data-letter={letter} key={`${letter}-${index}`}>
              {letter}
            </span>
          ))}
          <PlayCircleIcon className={` max-w-[1.4rem] ms-[3px]`} />
        </span>
        <span
          aria-hidden
          className="pointer-events-none absolute inset-0 -z-10 block"
        >
          {Array.from({ length: 20 }).map((_, index) => (
            <PlayCircleIcon
              key={index}
              className={`absolute left-1/2 top-1/2 opacity-0 sparkle-${index} max-w-[1.4rem] text-teal-300`}
            />
          ))}
        </span>
      </button>
    </div>
  );
}

export default AnimatedButton;
