export function goToPlaylistSection() {
  const playlistSectionElement = document.getElementById("playlist-section");
  playlistSectionElement.scrollIntoView({
    behavior: "smooth",
    block: "start",
    inline: "nearest",
  });
}

export function goToContactSection() {
  const contactSectionElement = document.getElementById("contact-text");
  contactSectionElement.scrollIntoView({
    behavior: "smooth",
    block: "center",
    inline: "nearest",
  });
}

