import FireBase from "../firebase/firebase";
import { useState } from "react";
import { getFirestore, collection, addDoc } from "firebase/firestore/lite";
import ButtonWrapper from "../shared/SpotLightButton";

export default function NewsLetter() {
  const [email, setEmail] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    const db = getFirestore(FireBase);
    const emailColection = collection(db, "emails");
    await addDoc(emailColection, {
      id: Math.random().toString(),
      email,
    });

    setEmail("");
  };

  return (
    <div className="text-white mb-20">
      <div className="flex flex-wrap md:flex-row justify-center gap-y-12 gap-x-1 items-start">
        {/* text side */}
        <div className="max-w-xl tracking-tight w-full text-center lg:text-left">
          <h2 id="newsletter" className="text-xl sm:text-2xl font-bold mb-5">
            Join our newsletter and get the latest news from studioseven.ai
            straight to your inbox
          </h2>
          <p className="text-gray-300">
            We will not share or sell your data with anyone else. Unsubscribe
            anytime.
          </p>
        </div>

        {/* form */}
        <form className="w-full max-w-md lg:col-span-5 lg:pt-2">
          <div className="flex flex-wrap justify-center gap-y-4 gap-x-4">
            <label htmlFor="email-address" className="sr-only">
              Email address
            </label>
            <input
              value={email}
              id="email-address"
              name="email"
              type="email"
              autoComplete="email"
              required
              className="outline-none text-black min-w-0 flex-auto rounded-md border-0 px-3.5 py-2 placeholder:text-gray-400 sm:text-sm"
              placeholder="Enter your email"
              onChange={(e) => {
                setEmail(e.target.value);
              }}
            />
            <ButtonWrapper text="Subscribe" onClick={handleSubmit} width="max-w-max" />

            {/* <button
              type="submit"
              className="flex-none rounded-md bg-teal-400 hover:bg-teal-400 text-gray-900 px-3.5 py-2.5 text-sm font-bold"
              onClick={handleSubmit}
            >
              Subscribe
            </button> */}
          </div>
        </form>
      </div>
    </div>
  );
}
