import { useState } from "react";

import TableView from "./darkTableView";
import AudioPlayer from "./AudioPlayer";
import Contact from "./pages/contact";
import FAQ from "./pages/faq";
import Blog from "./pages/blog";

import Feature from "./pages/features";
import ToolsSection from "./components/ToolsSection";
import NavBar from "./components/NavBar";
import Footer from "./components/Footer";
import Quote from "./components/Quote";
import Team from "./components/Team";
import { Slider } from "./pages/slider";

export default function LandingPage() {
  const [playAudio, setPlayAudio] = useState(false);
  const [currentStreamIndex, setCurrentStreamIndex] = useState(0);

  const toggleStream = () => {
    setPlayAudio((prev) => !prev);
  };

  function changeAndPlayStream(streamIdx = 0) {
    setCurrentStreamIndex(streamIdx);
    setPlayAudio(true);
  }

  return (
    <>
      <NavBar changeAndPlayStream={changeAndPlayStream} />

      <div className="bg-white">
        <div className="relative overflow-hidden">
          <main>
            <Slider />
            <Blog changeAndPlayStream={changeAndPlayStream} />
            <Feature />
            <TableView />
            <Quote />
            <ToolsSection />
            <Team />
            <Contact />
            <FAQ />
            <AudioPlayer
              playAudio={playAudio}
              togglePlay={toggleStream}
              setPlayAudio={setPlayAudio}
              currentStreamIndex={currentStreamIndex}
            />
          </main>

          <Footer />
        </div>
      </div>
    </>
  );
}
