import userIcon from "../assets/logos/user.svg";
import soundIcon from "../assets/logos/sound.svg";
import speakerIcon from "../assets/logos/speaker.svg";
import dataflowIcon from "../assets/logos/dataflow.svg";
import { motion, useTransform, useScroll } from "framer-motion";
import { useRef } from "react";
import Reveal from "../shared/Reveal";

const features = [
  {
    name: "Authentic Voices",
    description: "Genuine broadcast voices exclusive to your radio station",
    icon: userIcon,
  },
  {
    name: "Natural Language",
    description: "Natural, professional, presentation of shows and bulletins",
    icon: soundIcon,
  },
  {
    name: "Dynamic Content",
    description: "Dynamic news and information relevant to your listener",
    icon: speakerIcon,
  },
  {
    name: "Integrated System",
    description: "Full playout integration for a seamless output",
    icon: dataflowIcon,
  },
];

export default function Example() {
  return (
    <div className="relative bg-white mb-20 mt-16 sm:mt-24">
      <div className="mx-auto max-w-md px-6 text-center sm:max-w-3xl lg:max-w-7xl lg:px-8">
        <Reveal>
          <h2
            id="features"
            className="text-base sm:text-lg font-semibold text-teal-600"
          >
            FEATURES
          </h2>
        </Reveal>
        <Reveal>
          <p className="mt-2 text-2xl sm:text-3xl md:text-4xl font-bold tracking-tight text-gray-900">
            Get the help you need to make more listeners listen longer
          </p>
        </Reveal>
        <div className="mt-12">
          <HorizontalScrollCarousel />
        </div>
      </div>
    </div>
  );
}


const HorizontalScrollCarousel = () => {
  const targetRef = useRef(null);
  const { scrollYProgress } = useScroll({
    target: targetRef,
  });

  const x = useTransform(scrollYProgress, [0, 1], ["100%", "-95%"]);

  return (
    <section ref={targetRef} className="relative">
      <div className="max-w-4xl mx-auto gap-8 flex items-center justify-center overflow-hidden">
        <motion.div style={{ x }} className="flex gap-4">
          {features.map((feature, index) => {
            return <Card feature={feature} key={index} index={index} />;
          })}
        </motion.div>

      </div>
    </section>
  );
};

const Card = ({ feature, index }) => {
  const fadeInAnimation = {
    initial: {
      opacity: 0,
      y: 100
    },
    animate: (index) => ({
      opacity: 1,
      y: 0,
      transition: {
        delay: 0.05 * index
      }
    })
  }
  return (
    <motion.div
      variants={fadeInAnimation}
      initial="initial"
      whileInView="animate"
      custom={index}
      className="pt-6 min-w-[20rem]">
      <div className="flow-root rounded-[2rem] bg-[#f4f5fb] p-[30px] h-full">
        <div className="-mt-6">
          <div>
            <span className="inline-flex items-center justify-center rounded-md bg-gradient-to-r from-teal-300 to-teal-600 p-3 shadow-lg">
              <img
                className="h-6 w-6 text-white"
                src={feature.icon}
                alt={feature.name}
              />
            </span>
          </div>
          <h3 className="mt-8 text-[22px] tracking-tight font-bold text-gray-900">
            {feature.name}
          </h3>
          <p className="mt-5 text-base text-gray-500 text-[18px] font-medium">
            {feature.description}
          </p>
        </div>
      </div>
    </motion.div>

  );
};


