export default function TableView() {
  const [playingAudioId, setPlayingAudioId] = useState(null);

  const togglePlay = (audioId) => {
    if (playingAudioId === audioId) {
      setPlayingAudioId(null);
    } else {
      setPlayingAudioId(audioId);
    }
  };
  return (
    <div className="relative pt-20 bg-gray-900 text-white">
      {/* table header text */}
      <div className="mx-auto pt-8 pb-20 max-w-md px-6 text-center sm:max-w-4xl lg:px-8">
        <h2 className="text-lg font-semibold text-teal-300">WHAT YOU GET</h2>
        <p className="mt-2 text-2xl font-bold tracking-tight  sm:text-4xl">
          Studio Seven AI can help make better radio
        </p>
        <p className="mx-auto mt-5 max-w-prose text-lg sm:text-xl text-gray-200 font-light">
          Whether you want a complete low-cost radio station solution, or you want to improve your current output with more dynamic, interesting and entertaining content, we’ve got you covered.
        </p>
      </div>
      <div className="bg-gray-900 ">
        <h2 className="max-w-screen-xl mb-6 mx-auto text-base font-semibold leading-7 text-white">
          Example Playlist with AI
        </h2>
        <div className="max-h-[30rem] max-w-screen-xl mx-auto overflow-y-auto rounded-xl tableScroll">
          <table className=" w-full whitespace-nowrap text-left max-h-2 bg-gray-50 border-collapse">
            <colgroup>
              <col className="w-full sm:w-1/12" />
              <col className="md:w-5/12" />
              <col className="md:w-2/12" />
              <col className="md:w-1/12" />
              <col className="md:w-1/8" />
            </colgroup>
            <thead className="border-b border-white/10 text-sm leading-6 text-white bg-gray-700">
              <tr>
                <th
                  scope="col"
                  className="py-4 pl-4 pr-8 font-semibold sm:pl-6 lg:pl-8"
                >
                  Name
                </th>
                <th
                  scope="col"
                  className="hidden py-4 pl-0 pr-8 font-semibold sm:table-cell"
                >
                  Title
                </th>
                <th
                  scope="col"
                  className="py-4 pl-0 pr-4 text-right font-semibold sm:pr-8 sm:text-left lg:pr-20"
                >
                  Status
                </th>
                <th
                  scope="col"
                  className="hidden py-4 pl-0 pr-8 font-semibold md:table-cell lg:pr-20"
                >
                  Role
                </th>
                {/* <th
								scope="col"
								className="hidden py-2 pl-0 pr-4 text-right font-semibold sm:table-cell sm:pr-6 lg:pr-8"
							></th> */}
              </tr>
            </thead>
            <tbody className="divide-y divide-white/5">
              {activityItems.map((item, index) => (
                <tr key={item.commit + index}>
                  <td className="py-4 pl-4 pr-8 sm:pl-6 lg:pl-8">
                    <div
                      className="flex items-center text-white bg-teal-600 rounded-full p-2 w-fit"
                      onClick={() => togglePlay(item.id)}
                    >
                      {playingAudioId === item.id ? (
                        <PauseIcon className="w-5 h-5" />
                      ) : (
                        <PlayIcon className="w-5 h-5" />
                      )}
                    </div>
                    {playingAudioId === item.id && (
                      <audio
                        src={item.audioUrl}
                        autoPlay={playingAudioId === item.id}
                        onEnded={() => setPlayingAudioId(null)}
                      />
                    )}
                  </td>
                  <td className="hidden py-4 pl-0 pr-4 sm:table-cell sm:pr-8">
                    <div className="flex gap-x-3">
                      <div
                        style={{ color: item.color }}
                        className={`font-mono text-sm leading-6
									`}
                      >
                        {item.commit}
                      </div>
                    </div>
                  </td>
                  <td className="py-4 pl-0 pr-4 text-sm leading-6 sm:pr-8 lg:pr-20">
                    <div className="flex items-center justify-end gap-x-2 sm:justify-start">
                      <time
                        className="text-gray-400 sm:hidden"
                        dateTime={item.dateTime}
                      >
                        {item.date}
                      </time>
                      <div
                        className={classNames(
                          statuses[item.status],
                          "flex-none rounded-full p-1"
                        )}
                      >
                        <div className="h-1.5 w-1.5 rounded-full bg-current" />
                      </div>
                      <div className="hidden  sm:block text-gray-800">
                        {item.status}
                      </div>
                    </div>
                  </td>
                  <td
                    className={`hidden py-4 pl-0 pr-8 text-sm leading-6 text-gray-400 md:table-cell lg:pr-20 first-letter:
            
            `}
                  >
                    <span
                      className={`px-1.5 py-1 rounded text-xs font-medium
bg-gray-50 text-gray-800`}
                    >
                      {item.duration}
                    </span>
                  </td>
                  {/* <td className="hidden py-4 pl-0 pr-4 text-right text-sm leading-6 text-gray-400 sm:table-cell sm:pr-6 lg:pr-8">
									<button className="border border-gray-600 rounded px-4 py-1">
										Edit
									</button>
								</td> */}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
