import React, { useState, useCallback, useRef, useEffect, Fragment } from "react";
import { useWavesurfer } from '@wavesurfer/react'
import edmImg from "./assets/img/aitop40-logo.svg";


const streamUrls = [
  "https://centova.gptradio.co/_proxy/8001/stream",
  "https://centova.gptradio.co/_proxy/8004/stream",
];

function LoadingSpinner() {
  return (
    <div role="status">
      <svg
        aria-hidden="true"
        className="w-8 h-8 mr-2 text-gray-900 animate-spin dark:text-gray-900 fill-blue-600"
        viewBox="0 0 100 101"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
          fill="currentColor"
        />
        <path
          d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
          fill="currentFill"
        />
      </svg>
      <span className="sr-only">Loading...</span>
    </div>
  );
}

export default function AudioPlayer({
  playAudio,
  setPlayAudio,
  currentStreamIndex,
}) {
  const [volume, setVolume] = useState(0.5);
  const [isLoadingAudio, setIsLoadingAudio] = useState(true);
  const [isPlaying, setIsPlaying] = useState(false);

  //Wavesurfer Player

  const audioUrls = [
    "https://www.mfiles.co.uk/mp3-downloads/brahms-st-anthony-chorale-theme-two-pianos.mp3",
    "https://www.mfiles.co.uk/mp3-downloads/franz-schubert-standchen-serenade.mp3"
  ];

  const containerRef = useRef(null)

  const { wavesurfer } = useWavesurfer({
    container: containerRef,
    height: 25,
    barAlign: "bottom",
    barWidth: 2,
    hideScrollbar: true,
    barHeight: 2.7,
    waveColor: '#ffffff',
    progressColor: '#2DD4BF',
    url: audioUrls[currentStreamIndex],
    volume: 0.5
  })

  useEffect(() => {
    if (wavesurfer) {
      wavesurfer.on('ready', () => {
        setIsLoadingAudio(false);
      });
      wavesurfer.on('play', () => {
        setIsPlaying(true);
      });
      wavesurfer.on('pause', () => {
        setIsPlaying(false);
      });
    }
  }, [wavesurfer]);

  useEffect(() => {
    if (wavesurfer) {
      wavesurfer.on('finish', () => {
        setPlayAudio(false)
        setIsPlaying(false);
      });
    }
  }, [wavesurfer, setPlayAudio]);

  function calculateVolume(decimalPercentage) {
    const volumePercentage = parseFloat(decimalPercentage);
    if (isNaN(volumePercentage) || volumePercentage < 0 || volumePercentage > 100) {
      throw new Error('Invalid volume percentage. Please provide a number between 0 and 100.');
    }
    const volumeDecimal = volumePercentage / 100;
    return volumeDecimal;
  }

  const onPlayPause = useCallback(() => {
    wavesurfer && wavesurfer.playPause()
  }, [wavesurfer])

  useEffect(() => {
    const tooltipPauseBTN = document.querySelector(
      '[data-tooltip-target="tooltip-pause"]'
    );
    if (tooltipPauseBTN && playAudio) {
      setTimeout(() => {
        tooltipPauseBTN.click();
      }, 500);
    }
  }, [currentStreamIndex, playAudio]);

  // ------------------------------


  const handleVolumeChange = (event) => {
    const newVolume = parseFloat(event.target.value);
    setVolume(newVolume);
  };

  const setPlayerVolume = useCallback(() => {
    if (wavesurfer) {
      const playerVolume = calculateVolume(volume)
      wavesurfer.setVolume(playerVolume);
    }
  }, [wavesurfer, volume]);

  useEffect(() => {
    setPlayerVolume();
  }, [setPlayerVolume]);
  return (
    <div className="fixed bottom-0 left-0 z-50 w-full h-[6rem] bg-gradient-to-r from-black to-gray-900">
      <div className="max-w-6xl grid px-8 w-full mx-auto h-full grid-cols-3 ">
        <div className="w-full flex items-center justify-start mr-auto gap-2">
          <img className="h-12 mr-3 rounded" src={edmImg} alt="Video preview" />
        </div>
        <div className="flex items-center w-full">
          <div className="w-full">
            <div className={`flex flex-col gap-2 ${isLoadingAudio ? "items-center" : ""}`}>
              {isLoadingAudio ? (
                <LoadingSpinner />
              ) : (
                <div className="flex items-center gap-2 justify-start w-full">
                  <button
                    onClick={onPlayPause}
                    data-tooltip-target="tooltip-pause"
                    type="button"
                  >

                    <svg version="1.1" id="play" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 100 100" enableBackground="new 0 0 100 100" xmlSpace="preserve">
                      <path className="stroke-solid" fill="none" stroke="#ffffff" d="M49.9,2.5C23.6,2.8,2.1,24.4,2.5,50.4C2.9,76.5,24.7,98,50.3,97.5c26.4-0.6,47.4-21.8,47.2-47.7
                        C97.3,23.7,75.7,2.3,49.9,2.5" />
                      {isPlaying ? (
                        <>
                          <path d="M8.5 17.9V2.1C8.5 0.6 7.86667 0 6.26667 0H2.23333C0.633334 0 0 0.6 0 2.1V17.9C0 19.4 0.633334 20 2.23333 20H6.26667C7.86667 20 8.5 19.4 8.5 17.9Z" fill="#ffffff" className="transform translate-x-[32px] translate-y-[28px] scale-[2]" />
                          <path d="M20 17.9V2.1C20 0.6 19.3667 0 17.7667 0H13.7333C12.1444 0 11.5 0.6 11.5 2.1V17.9C11.5 19.4 12.1333 20 13.7333 20H17.7667C19.3667 20 20 19.4 20 17.9Z" fill="#ffffff" className="transform translate-x-[32px] translate-y-[28px] scale-[2]" />
                        </>
                      ) : (
                        <>
                          <path d="M18.2804 6.84481L0.174099 16.9104C0.062178 16.5299 0 16.1264 0 15.7113V4.94238C0 1.39118 4.14107 -0.822554 7.46138 0.953045L12.4854 3.6395L17.5218 6.33749C17.7954 6.48738 18.0565 6.6488 18.2804 6.84481Z" fill="#ffffff" className="transform translate-x-[32px] translate-y-[28px] scale-[2]" />
                          <path d="M17.5224 14.3162L12.486 17.0141L7.46201 19.7006C5.0868 20.9689 2.28879 20.2079 0.895996 18.3516L1.41829 18.0634L19.3753 8.07849C20.6189 10.1539 20.0096 12.9902 17.5224 14.3162Z" fill="#ffffff" className="transform translate-x-[32px] translate-y-[28px] scale-[2]" />
                        </>
                      )}
                    </svg>

                    <span className="sr-only">Pause video</span>
                  </button>
                  <span className="text-sm w-max text-white">
                    {currentStreamIndex === 1
                      ? "AI Top 40 with Nexus James"
                      : "Ai Radio One"}
                  </span>
                </div>
              )}
              <div ref={containerRef} className="w-full" />
            </div>
          </div>
        </div>


        <div className="items-center justify-center ml-auto flex ">
          <div className="group relative cursor-pointer volume-b">
            {volume === 0 ? <svg className="w-5 h-5 text-white" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M12.4727 11.799C12.3536 11.799 12.2419 11.7589 12.1377 11.6789C11.8919 11.4789 11.8398 11.1028 12.026 10.8387C12.9642 9.49438 13.1653 7.70993 12.5621 6.16555C12.443 5.86147 12.577 5.50938 12.86 5.38135C13.1429 5.25332 13.4706 5.39736 13.5897 5.70143C14.3492 7.63792 14.0886 9.88648 12.9195 11.5669C12.8078 11.7189 12.644 11.799 12.4727 11.799Z" fill="white" />
              <path d="M13.8351 13.7995C13.7159 13.7995 13.6042 13.7595 13.5 13.6794C13.2542 13.4794 13.2021 13.1033 13.3883 12.8392C14.9818 10.5587 15.3318 7.50189 14.3042 4.86923C14.185 4.56515 14.3191 4.21307 14.602 4.08503C14.885 3.957 15.2126 4.10104 15.3318 4.40511C16.5158 7.42987 16.1137 10.9347 14.2818 13.5594C14.1776 13.7194 14.0063 13.7995 13.8351 13.7995Z" fill="white" />
              <path d="M9.52332 8.76619C9.99245 8.26206 10.7967 8.62215 10.7967 9.33433V11.6789C10.7967 13.0553 10.335 14.0875 9.50843 14.5836C9.17334 14.7837 8.80102 14.8797 8.41381 14.8797C7.8181 14.8797 7.1777 14.6637 6.52242 14.2236L6.04585 13.9035C5.64374 13.6314 5.57673 13.0233 5.91182 12.6632L9.52332 8.76619Z" fill="white" />
              <path d="M9.50906 1.42035C8.67507 0.924229 7.61023 1.05226 6.52305 1.78044L4.3487 3.24481C4.19977 3.34083 4.02851 3.39685 3.85724 3.39685H3.16472H2.7924C0.990372 3.39685 0 4.46111 0 6.3976V9.5984C0 11.5349 0.990372 12.5991 2.7924 12.5991H3.16472H3.7232L10.7973 4.99725V4.32508C10.7973 2.94873 10.3356 1.91648 9.50906 1.42035Z" fill="white" />
              <path d="M15.2793 0.180045C15.056 -0.060015 14.6911 -0.060015 14.4677 0.180045L0.729068 14.9517C0.505675 15.1918 0.505675 15.5839 0.729068 15.824C0.840764 15.936 0.982245 16 1.13117 16C1.2801 16 1.42158 15.936 1.53328 15.816L15.2793 1.04426C15.5102 0.804201 15.5102 0.420105 15.2793 0.180045Z" fill="white" />
            </svg> : <svg className="w-5 h-5 text-white" viewBox="0 0 16 14" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M12.4753 10.8711C12.3561 10.8711 12.2444 10.8304 12.1401 10.7489C11.8944 10.5452 11.8422 10.1622 12.0284 9.89331C13.1978 8.19032 13.1978 5.81101 12.0284 4.10802C11.8422 3.83913 11.8944 3.45616 12.1401 3.25245C12.3859 3.04874 12.736 3.10578 12.9222 3.37468C14.382 5.50953 14.382 8.4918 12.9222 10.6267C12.8105 10.7896 12.6466 10.8711 12.4753 10.8711Z" fill="white" />
              <path d="M13.8386 12.9081C13.7194 12.9081 13.6077 12.8674 13.5034 12.7859C13.2576 12.5822 13.2055 12.1992 13.3917 11.9303C15.3803 9.02955 15.3803 4.9717 13.3917 2.07091C13.2055 1.80202 13.2576 1.41905 13.5034 1.21534C13.7492 1.01163 14.0993 1.06867 14.2855 1.33757C16.572 4.67022 16.572 9.33104 14.2855 12.6637C14.1812 12.8267 14.0099 12.9081 13.8386 12.9081Z" fill="white" />
              <path d="M10.7992 3.26055V10.7407C10.7992 12.1422 10.3374 13.1933 9.51071 13.6985C9.17555 13.9022 8.80315 14 8.41586 14C7.82002 14 7.1795 13.78 6.52407 13.3318L4.34927 11.8407C4.20031 11.7429 4.029 11.6859 3.8577 11.6859H3.16504V2.31535H3.8577C4.029 2.31535 4.20031 2.25832 4.34927 2.16054L6.52407 0.669399C7.61148 -0.0720955 8.67654 -0.202468 9.51071 0.302726C10.3374 0.80792 10.7992 1.85905 10.7992 3.26055Z" fill="white" />
              <path d="M3.16539 2.31531V11.6858H2.79299C0.99058 11.6858 0 10.6021 0 8.63023V5.37092C0 3.39903 0.99058 2.31531 2.79299 2.31531H3.16539Z" fill="white" />
            </svg>}

            <div className="absolute p-2 hidden range-c group-hover:flex hover:flex w-[120px] right-[-50px] top-[-80px] transform rotate-[270deg]">
              <div className="bg-white rounded-md p-2 flex">
                <input
                  type="range"
                  min="0"
                  max="100"
                  value={volume}
                  onChange={handleVolumeChange}
                  className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer dark:bg-gray-700"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
