import NewsLetter from "../pages/newsletter";
import {
  goToPlaylistSection,
  goToContactSection,
  goToFaq,
  goToFeatres,
  goToNewsLetter,
  goToAboutUs,
  goToTwitterPage,
  goToLinkedinPage,
} from "../utils/scrollFunctions";

const footerNavigation = [
  {
    title: "demos",
    footerLinks: [
      { text: "AI Top 40", link: goToPlaylistSection },
      { text: "AI Radio One", link: goToPlaylistSection },
    ],
  },
  {
    title: "product",
    footerLinks: [
      { text: "features", link: goToFeatres },
      { text: "example playlist", link: goToPlaylistSection },
    ],
  },
  {
    title: "info",
    footerLinks: [
      { text: "about us", link: goToAboutUs },
      { text: "newsletter", link: goToNewsLetter },
    ],
  },

  {
    title: "help",
    footerLinks: [
      { text: "get in touch", link: goToContactSection },
      { text: "FAQs", link: goToFaq },
    ],
  },
  {
    title: "social",
    footerLinks: [
      { text: "LinkedIn", link: goToLinkedinPage },
      { text: "X", link: goToTwitterPage },
    ],
  },
];

export default function Footer() {
  return (
    <footer
      className="bg-gray-900 text-white pb-24 pt-24"
      aria-labelledby="footer-heading"
    >
      <div className="mx-auto max-w-md px-6 sm:max-w-7xl lg:px-8">
        <NewsLetter />

        {/* footer links */}
        <ul className="py-16 border-y border-gray-500 grid grid-cols-2 sm:grid-cols-3 md:grid-cols-5 mx-auto gap-7 capitalize">
          {footerNavigation.map(({ title, footerLinks }) => {
            return (
              <li key={title} className="md:mx-auto text-center md:text-left">
                <h3 className="text-teal-300 mb-4">{title}</h3>
                <ul className="flex flex-col gap-1">
                  {footerLinks.map(({ text, link }) => {
                    return (
                      <li key={text}>
                        <button
                          onClick={link}
                          className="hover:underline cursor-pointer"
                        >
                          {text}
                        </button>
                      </li>
                    );
                  })}
                </ul>
              </li>
            );
          })}
        </ul>

        {/* trademark */}
        <div className="mt-12 pb-8 pt-1">
          <p className="text-base  text-center">
            &copy; 2023 StudioSeven AI . All rights reserved.
          </p>
        </div>
      </div>
    </footer>
  );
}
