import { motion } from "framer-motion";
import { useEffect, useRef } from "react";

const ButtonWrapper = ({ text, width, icon, bgColor, ...props }) => {
  return (
    <>
      <SpotlightButton
        text={text}
        icon={icon}
        {...props}
        bgColor={bgColor}
        width={width}
      />
    </>
  );
};

const SpotlightButton = ({ text, width, icon, bgColor, ...props }) => {
  const btnRef = useRef(null);
  const spanRef = useRef(null);

  useEffect(() => {
    const handleMouseMove = (e) => {
      const { width } = e.target.getBoundingClientRect();
      const offset = e.offsetX;
      const left = `${(offset / width) * 100}%`;

      spanRef.current.animate({ left }, { duration: 250, fill: "forwards" });
    };

    const handleMouseLeave = () => {
      spanRef.current.animate(
        { left: "50%" },
        { duration: 100, fill: "forwards" }
      );
    };

    btnRef?.current?.addEventListener("mousemove", handleMouseMove);
    btnRef?.current?.addEventListener("mouseleave", handleMouseLeave);

    return () => {
      btnRef?.current?.removeEventListener("mousemove", handleMouseMove);
      btnRef?.current?.removeEventListener("mouseleave", handleMouseLeave);
    };
  }, []);

  return (
    <motion.button
      whileTap={{ scale: 0.985 }}
      ref={btnRef}
      {...props}
      className={`group relative w-full ${width} overflow-hidden  ${
        bgColor === "white" ? "bg-white" : "bg-teal-400"
      } flex items-center justify-center gap-1 text-center text-white font-semibold px-3 py-2 rounded-lg outline-none`}
    >
      <span
        className={`pointer-events-none relative z-10 mix-blend-difference ${
          bgColor === "white" ? "" : "text-[#66cdaa] group-hover:text-[unset]"
        } flex items-center gap-1`}
      >
        {text}
        {icon}
      </span>
      <span
        ref={spanRef}
        className={`pointer-events-none absolute left-[50%] top-[50%] h-32 w-32 -translate-x-[50%] -translate-y-[50%] rounded-full text-black ${
          bgColor === "white"
            ? "group-hover:bg-teal-400"
            : "bg-teal-400 group-hover:bg-slate-100"
        }`}
      />
    </motion.button>
  );
};

export default ButtonWrapper;
