import aiTop40Img from "../assets/img/aiTop40.jpg";
import radioOneImg from "../assets/img/radioOne.jpg";
import aiTop40Logo from "../assets/logos/aitop40Logo.png";
import radioOneLogo from "../assets/logos/radioOneLogo.png";
import AnimatedButton from "../shared/AnimatedButton";
import Reveal from "../shared/Reveal";

const exampleStations = [
  {
    title: "Ai Radio One",
    description: "Listen to the world’s most advanced AI-driven radio station.",
    link: "https://radiostation.ai/",
    img: radioOneImg,
    logo: radioOneLogo,
  },
  {
    title: "AI Top 40",
    description:
      "Listen to the world’s first chart show produced and presented by AI.",
    link: "https://aitop40.com/",
    img: aiTop40Img,
    logo: aiTop40Logo,
  },
];
export default function Blog({ changeAndPlayStream }) {
  return (
    <div className="relative bg-gray-50 py-16 sm:py-24 lg:py-32">
      <div className="relative">
        <div className="mx-auto max-w-md px-6 text-center sm:max-w-3xl lg:max-w-7xl lg:px-8">
          <Reveal>
            <h2 className="text-lg font-semibold text-teal-600">
              RADIO REVOLUTIONISED
            </h2>
          </Reveal>
          <Reveal>
            <p className="mt-2 text-2xl font-bold tracking-tight text-gray-900 sm:text-4xl">
              Use AI to create authentic, engaging, radio
            </p>
          </Reveal>
          <Reveal>
            <p className="mx-auto mt-5 max-w-prose text-lg sm:text-xl text-gray-500">
              Listen to Studio Seven’s AI radio stations and hear for yourself how
              good radio entirely produced and presented by AI can be.
            </p>
          </Reveal>
        </div>
        <div
          id="playlist-section"
          className="justify-center mx-auto mt-12 grid grid-cols-[repeat(auto-fit,minmax(16rem,24rem))] max-w-md gap-8 px-6 sm:max-w-lg lg:max-w-7xl"
        >

          {exampleStations.map(
            ({ title, description, link, img, logo }, index) => (
              <Reveal key={index}
                hidden={{ opacity: 0, x: index === 0 ? -75 : 75 }}
                visible={{ opacity: 1, x: 0 }}>
                <div

                  className="text-sm md:text-base overflow-hidden flex items-center min-h-[28rem] md:min-h-[31.25rem] isolate relative max-w-sm rounded-[12rem] shadow bg-gray-800 text-white group"
                >
                  <img
                    className="absolute -z-10 object-cover top-0 left-0 object-center w-full h-full rounded-t-lg group-hover:transform group-hover:scale-110 transition duration-500"
                    src={img}
                    alt={title}
                  />
                  <div className="px-5 bg-gray-900/70 backdrop-blur-md group-hover:opacity-[1] opacity-[0] flex justify-center min-h-[28rem] md:min-h-[31.25rem] transition duration-500 delay-75 h-full flex-col py-12 group-hover:h-full">
                    <div className="flex gap-4 items-center mb-4">
                      <img
                        className="max-w-[2rem]"
                        src={logo}
                        alt={title + " logo"}
                      />
                      <h5 className="text-lg md:text-2xl font-semibold">
                        {title}
                      </h5>
                    </div>
                    <p className="mb-6 font-normal">{description}</p>
                    <AnimatedButton onClick={() => changeAndPlayStream(index)} className="w-full bg-teal-400 hover:bg-teal-400 flex items-center justify-center gap-1 text-center text-black font-semibold px-3 py-2 rounded-lg outline-none" string="Listen to our Demo" />
                    {/* <button
                    onClick={() => changeAndPlayStream(index)}
                    className="w-full bg-teal-400 hover:bg-teal-400 flex items-center justify-center gap-1 text-center text-black font-semibold px-3 py-2 rounded-lg outline-none"
                  >
                    Listen to our Demo
                  </button> */}
                  </div>
                </div>
              </Reveal>

            )
          )}
        </div>
      </div>
    </div>
  );
}
