import { Disclosure } from "@headlessui/react";
import { MinusSmallIcon, PlusSmallIcon } from "@heroicons/react/24/outline";
import { motion } from "framer-motion";
import Reveal from "../shared/Reveal";


const faqs = [
  {
    question: "How much does it cost?",
    answer:
      "We’d love to give you a definitive answer, but it depends on market size and use case. Get in touch and we can figure out exactly what you need.",
  },

  {
    question: "Is it market-exclusive?",
    answer:
      "The AI voices and AI content can be unique in your market. They will be defined by your radio station’s target listener, brand values and voice characters.",
  },

  {
    question: "What makes studioseven.ai different from everyone else?",
    answer:
      "We are an experienced team of radio professionals who have created a bespoke set of broadcast tools. Whilst other people have tried to use cheap, generic AI tools we have built a bespoke, integrated system to help create an authentic radio experience. These tools include: Broadcast content engine, Broadcast language model, Broadcast voice model, Broadcast playout system",
  },

  {
    question: "How does it work?",
    answer:
      "Whether you want a complete AI Radio Station with our bespoke playout system that includes music and commercial scheduling, or you want to include AI Voice Tracking or AI Bulletins into your existing radio station or stream, we can integrate our system to ensure a simple, straight-forward approach to managing and optimising your output.",
  },

  {
    question: "How do you make it sound so natural?",
    answer:
      "Both the professional AI voices and the things the presenters say are modelled and trained by broadcast professionals. They have learned from talented professional presenters and are uniquely designed for radio to ensure an authentic radio experience.",
  },

  {
    question: "What are the next steps?",
    answer:
      "We’ll provide you with a personalized 30-minute onboarding call to get you up and running as soon as possible.",
  },

  // More questions...
];

export default function Example() {
  return (
    <div className="bg-gray-900">
      <div className="mx-auto max-w-7xl px-6 py-8 sm:py-6 lg:px-8 lg:py-40">
        <div className="mx-auto max-w-4xl divide-y divide-gray-900/10">
          <h2
            id="faq-title"
            className="text-2xl font-bold leading-10 tracking-tight text-white"
          >
            Frequently asked questions
          </h2>
          <dl className="mt-10 space-y-6 divide-y divide-gray-900/10">
            {faqs.map((faq) => (
              <Disclosure as="div" key={faq.question} className="pt-6">
                {({ open }) => (
                  <>
                    <dt>
                      <Disclosure.Button className={"w-full"}>
                        <motion.span
                          whileTap={{ scale: 0.97 }}
                          className="flex w-full items-start justify-between text-left text-white"                        >
                          <span className="text-base font-semibold leading-7">
                            {faq.question}
                          </span>
                          <span className="ml-6 flex h-7 items-center">
                            {open ? (
                              <MinusSmallIcon
                                className="h-6 w-6"
                                aria-hidden="true"
                              />
                            ) : (
                              <PlusSmallIcon
                                className="h-6 w-6"
                                aria-hidden="true"
                              />
                            )}
                          </span>
                        </motion.span>

                      </Disclosure.Button>
                    </dt>
                    <Disclosure.Panel as="dd" className="mt-2 pr-12">
                      <Reveal
                        hidden={{ opacity: 0, y: 20 }}
                        visible={{ opacity: 1, y: 0 }}
                      >
                        <p className="text-base leading-7 text-gray-500">
                          {faq.answer}
                        </p>
                      </Reveal>
                    </Disclosure.Panel>
                  </>
                )}
              </Disclosure>
            ))}
          </dl>
        </div>
      </div>
    </div>
  );
}
